.TodoList {
    display: flex;
    flex-direction: column;
    gap: 20px;
}

.TodoList>input {
    width: 100%;
    border: none;
    border-bottom: 1px solid rgb(220, 220, 220);
    padding: 15px 0px;
}

.TodoList>input:focus {
    outline: none;
    border-bottom: 1px solid rgb(37, 147, 255);
}

.TodoList .todos_wrapper {
    display: flex;
    flex-direction: column;
    gap: 20px;
}